.info__container {
  display: flex;
  width: 100%;
  height: 100%;
  font-family: Raleway;
  font-weight: regular;
  border-radius: 12px;
  flex-direction: column;
  background-color: #fff;
  margin: 30px 0px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
  padding-top: 5px;
}
.info__container__Black {
  display: flex;
  width: 100%;
  height: 100%;
  font-family: Raleway;
  font-weight: regular;
  border-radius: 12px;
  flex-direction: column;
  background-color: rgb(61, 70, 80);
  margin: 30px 0px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
  padding-top: 5px;
}

.info__container__Black p {
  color: white;
}
.info__container__Black span {
  color: white;
}

.info__header {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  height: 60px;
}

.info__UserName {
  margin: 10px 0px 0px 30px;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  /* padding-top: 10px; */
}

.info__UserTextContainer {
  margin: 0px 70px 0px 30px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.info__UserTextStatus {
  margin: 0px;
  padding: 5px 0px 10px 0px;
}

.info__UserOnlineStatus {
  margin: -10px -50px 0px 0px;
  opacity: 0.7;
  font-weight: Medium;
  font-family: Roboto;
  padding-bottom: 10px;
  /* font-size: 12px; */
}

.info__generalData__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 90%;
  max-width: 100%;
  border-bottom: 2px solid rgb(207, 198, 198);
  border-top: 2px solid rgb(207, 198, 198);
  margin-left: 20px;
  min-height: 135px;
  max-height: 100%;
}

.info__generalData__container div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  min-width: 50%;
  max-width: 100%;
  height: 30px;
}

.info__generalData__container article {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  min-width: 50%;
  max-width: 100%;
  height: 30px;
}

.info__generalData__container span {
  margin-left: 30px;
  margin-top: 0px;
}

.info__footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.info__footer__container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 80%;
  height: 100%;
}

.info__footer__container a:visited {
  color: black;
}
.info__footer__unit {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
  text-decoration: none;
}

.info__footer__unit__number {
  font-size: 20px;
  font-family: Roboto;
  font-weight: bold;
}

.info__footer__unit__text {
  margin-top: 10px;
  font-size: 14px;
  font-weight: medium;
}

.info__generalData__Birth__Date {
  font-family: Roboto;
  font-weight: 600;
}
